@media (min-width: 1500px) {
  .store-main {
    padding: 20px 300px;
  }
}
.store-main {
  background-color: #b2b4ac;
  color: #fddcc1;
  min-height: 85vh;
}

.all-products-wrap,
.all-cart-wrap {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

.product-item {
  margin: 20px;
  padding: 10px;
  border-radius: 5px;
  background-color: #1f201c;
  -webkit-box-shadow: 4px 3px 14px -4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 3px 14px -4px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 3px 14px -4px rgba(0, 0, 0, 0.75);
}

.product-info {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  border-bottom: 1px solid #585858;
}
.product-preview {
  height: 200px;
  background-repeat: no-repeat;
  background-position: center;
}

input {
  height: 50px;
  background-color: #d5c8ac;
  font-size: 1.3rem;
  border: #fddcc1;
  outline: 0;
  border-radius: 5px;
}

.product-name {
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 1px;
  margin-bottom: 3px;
}

.product-price {
  font-size: 16px;
}

.buttons-control {
  display: flex;
  flex-direction: column;
  margin: 5px;
}
.increment-decrement {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}
.custom-input {
  display: contents;
}
.product-bottom-info {
  display: flex;
  align-items: center;
  margin-top: 5px;
  letter-spacing: 1px;
  margin-left: 10px;
  justify-content: space-between;
}

.plus-one {
  background-color: #051e05;
}
.minus-one {
  background-color: #6c0f0f;
}
.add-button {
  background-color: #051e05;
  color: #fddcc1;
}
